import { _decorator, Component, Label, Node, Sprite, SpriteFrame } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('QuizDescription')
export class QuizDescription extends Component {

    @property({type:Sprite})
    private image = null;

    @property({type:Label})
    private level = null;

    @property({type:Label})
    private description = null;

    set(frame: SpriteFrame, level: number, levelCount: number, description: string) {
        this.image.spriteFrame = frame;
        //this.level.string = 'Уровень '+level+' ('+level+'/'+levelCount+')';
        this.level.string = 'Level '+level+' ('+level+'/'+levelCount+')';
        this.description.string = description;
    }
}
